'use client';

import React, { useEffect, useState } from 'react';
import { createPortal } from 'react-dom';
import { useUI } from '@/context/UIContext';
import { signInWithGoogle } from '@/lib/supabase/auth';

interface LoginModalProps {
  isOpen: boolean;
  onClose: () => void;
}

const LoginModal: React.FC<LoginModalProps> = ({ isOpen, onClose }) => {
  const [mounted, setMounted] = useState(false);
  const { loading } = useUI();

  useEffect(() => {
    setMounted(true);
  }, []);

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }
    
    return () => {
      document.body.style.overflow = '';
    };
  }, [isOpen]);

  const handleGoogleSignIn = async () => {
    try {
      console.log('Attempting Google sign in...');
      await signInWithGoogle();
      console.log('Sign in successful, closing modal');
      // Close the modal immediately - the redirect will handle the rest
      onClose();
    } catch (error) {
      console.error('Google sign-in error:', error);
      // Optionally show an error message to the user
      onClose(); // Close modal even on error to avoid being stuck
    }
  };

  if (!mounted) return null;

  return createPortal(
    <div
      className={`fixed inset-0 z-[999999] flex items-center justify-center p-4 bg-black bg-opacity-50 backdrop-blur-sm transition-opacity duration-300 ${isOpen ? 'opacity-100 visible' : 'opacity-0 invisible'}`}
      onClick={onClose}
      style={{ pointerEvents: isOpen ? 'auto' : 'none' }}
    >
      <div
        className="relative w-full max-w-md bg-white rounded-2xl shadow-2xl border border-gray-200 overflow-hidden transform transition-transform duration-300"
        onClick={(e) => e.stopPropagation()}
        style={{
          minHeight: '350px',
          maxHeight: '90vh',
          overflowY: 'auto',
          transform: isOpen ? 'scale(1)' : 'scale(0.95)',
        }}
      >
        {/* Header */}
        <div className="bg-gradient-to-r from-amber-50 to-amber-100 px-8 pt-10 pb-6 text-center border-b border-gray-200 relative">
          <button
            onClick={onClose}
            className="absolute right-4 top-4 h-8 w-8 rounded-full bg-white border border-gray-300 flex items-center justify-center text-gray-600 hover:text-gray-900 hover:bg-gray-100 transition-all duration-200 hover:rotate-90 z-10"
            aria-label="Close login modal"
          >
            <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>

          <h2 className="font-serif text-2xl font-semibold tracking-tight text-gray-900 mb-2">
            Welcome Back
          </h2>
          <p className="font-sans text-sm text-gray-600">
            Sign in to access your exclusive design experience
          </p>
        </div>

        {/* Content */}
        <div className="px-8 pb-8 bg-white">
          <button
            onClick={handleGoogleSignIn}
            disabled={loading?.auth}
            className="group w-full rounded-xl border border-gray-300 bg-white px-6 py-4 font-sans font-medium text-gray-700 shadow-sm hover:shadow-md hover:bg-gray-50 hover:border-gray-400 disabled:opacity-60 disabled:cursor-not-allowed transition-all duration-200 relative overflow-hidden"
          >
            <div className="absolute inset-0 bg-gradient-to-r from-amber-50 to-amber-100 opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
            <div className="relative flex items-center justify-center">
              <div className="mr-4 h-5 w-5 flex-shrink-0">
                <svg viewBox="0 0 24 24" className="h-5 w-5">
                  <path d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z" fill="#4285F4" />
                  <path d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z" fill="#34A853" />
                  <path d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z" fill="#FBBC05" />
                  <path d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z" fill="#EA4335" />
                </svg>
              </div>
              <span className="text-sm">{loading?.auth ? 'Signing in...' : 'Continue with Google'}</span>
              {loading?.auth && (
                <div className="ml-3 h-4 w-4 border-2 border-gray-200 border-t-amber-600 rounded-full animate-spin" />
              )}
            </div>
          </button>

          <div className="my-6">
            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-gray-300" />
              </div>
              <div className="relative flex justify-center">
                <span className="bg-white px-3 font-serif text-xs font-medium text-gray-500 tracking-wide">
                  EXCLUSIVE BENEFITS
                </span>
              </div>
            </div>
          </div>

          <div className="space-y-3">
            <div className="flex items-start p-3 rounded-lg hover:bg-gray-50 transition-colors">
              <div className="mr-3 flex h-8 w-8 flex-shrink-0 items-center justify-center rounded-full bg-gradient-to-br from-amber-100 to-amber-200 mt-0.5">
                <svg className="h-4 w-4 text-amber-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                </svg>
              </div>
              <span className="font-sans text-xs font-medium text-gray-700 flex-1">
                Personalized design recommendations
              </span>
            </div>

            <div className="flex items-start p-3 rounded-lg hover:bg-gray-50 transition-colors">
              <div className="mr-3 flex h-8 w-8 flex-shrink-0 items-center justify-center rounded-full bg-gradient-to-br from-amber-100 to-amber-200 mt-0.5">
                <svg className="h-4 w-4 text-amber-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z" />
                </svg>
              </div>
              <span className="font-sans text-xs font-medium text-gray-700 flex-1">
                Save your favorite pieces to collections
              </span>
            </div>

            <div className="flex items-start p-3 rounded-lg hover:bg-gray-50 transition-colors">
              <div className="mr-3 flex h-8 w-8 flex-shrink-0 items-center justify-center rounded-full bg-gradient-to-br from-amber-100 to-amber-200 mt-0.5">
                <svg className="h-4 w-4 text-amber-700" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
              </div>
              <span className="font-sans text-xs font-medium text-gray-700 flex-1">
                Priority booking for consultations
              </span>
            </div>
          </div>

          <div className="mt-6 pt-4 border-t border-gray-200">
            <p className="text-center font-sans text-xs text-gray-500">
              By continuing, you agree to our {' '}
              <a href="#" className="text-amber-700 hover:text-amber-800 font-medium underline-offset-2 hover:underline">
                Terms of Service
              </a>
              {' '} and {' '}
              <a href="#" className="text-amber-700 hover:text-amber-800 font-medium underline-offset-2 hover:underline">
                Privacy Policy
              </a>
            </p>
          </div>
        </div>
      </div>
    </div>,
    document.body
  );
};

export default LoginModal;